export class PdsDynamicSelect {
  is = 'pds-dynamic-select';
  extends = 'select';

  properties = {
    dynamicOptions: {
      observer: 'optionsUpdated'
    },
    defaultValue: {}
  };

  //Array of option tags that are going to be there.
  dynamicOptions: Array<{value: any, text: string}> = [];

  defaultValue: any;
  defaultUsed: boolean = false;

  optionsUpdated(): void {
    let testValue;
    if (this.defaultUsed) {
      testValue = (<any>this).value;
    } else {
      testValue = this.defaultValue;
      this.defaultUsed = true;
    }
    if (testValue == null) {
      testValue = (<any>this).value;
    }

    (<any>this).innerHTML = ''; //clear the previous children.
    for (let i = 0; i < this.dynamicOptions.length; i++) {
      let option = this.dynamicOptions[i];
      let optionEle = document.createElement('option');
      optionEle.value = option.value;
      optionEle.innerText = option.text;

      if (option.value === testValue) {
        optionEle.setAttribute('selected', '');
      } else {
        optionEle.removeAttribute('selected');
      }
      (<any>this).appendChild(optionEle);
    }
  }
}



