import {PdsWidget} from './PdsWidget';
export class PdsAppSwitcherWidget extends PdsWidget {
  is = 'pds-app-switcher';

  showApps: boolean = true;

  //Apps and plugins are properties. They can be bound via angular 2
  //or they can use javascript to query for it and set them manually.
  apps: Array<{title: string, href: string, id: string}>;

  plugins: Array<{title: string, href: string}>;

  classLookup: {[key: string]: string} = {
    powerTeacher: 'pds-pt-icon-xxl',
    powerTeacherPro: 'pds-ptp-icon-xxl',
    learning: 'pds-learning-icon-xxl',
    assessment: 'pds-assessment-icon-xxl',
    specialEducation: 'pds-sped-icon-xxl'
  };

  messageKeysUpdated(): void {
  }

  getIconClass(classLookupKey): string {
    return this.classLookup[classLookupKey];
  }

  ready(): void {
    document.body.addEventListener('click', (event) => {
      if (this.showApps && !this.pds.utils.getClosest(event.target, 'pds-apps-dropdown')) {
        this.close();
      }
    });

    //test data
    this.apps = [];

    this.plugins = [];
  }

  openAppSwitcher(event): void {
    event.preventDefault();
    if (!this.showApps) {
      setTimeout(() => {
        this.showApps = true;
      }, 0);
    }
  }

  close(): void {
    this.showApps = false;
  }
}



