import {PdsAbstractInputWidget} from './PdsAbstractInputWidget';

export class PdsDateWidget extends PdsAbstractInputWidget {
  is = 'pds-date-widget';

  name: string;

  dataMaxdate: string;
  dataMindate: string;
  dataMaxdate_exclusive: string;
  dataMindate_exclusive: string;

  buttonText: string;

  // properties = {
  //   dataMaxdate: String,
  //   dataMindate: String,
  //   dataMaxdate_exclusive: String,
  //   dataMindate_exclusive: String,
  //   name: String
  // };

  properties = {
    name: String,                   //works
    dataLabelText: String,          //works
    dataBadgeText: String,
    dataHelperText: String,
    dataRegexErrorText: String,
    dataFieldHelpText: String,
    dataPlaceholderText: String,
    dataTooltipText: String,
    dataIsrequired: {
      type: Boolean,
      value: false,
      observer: 'setRequired',
      notify: true
    },
    dataIsreadonly: {
      type: Boolean,
      value: false,
      observer: 'setReadonly',
      notify: true
    },
    hasHadError: {
      value: false,
      observer: 'setShowErrorMessage'
    },
    dataMaxdate: String,
    dataMindate: String,
    dataMaxdate_exclusive: String,
    dataMindate_exclusive: String
  };

  dialogValue: Date;
  showDatePickerDialog: boolean = false;

  ready(): void {
    this.messageKeysUpdated();
  }

  messageKeysUpdated(): void {
    this.buttonText = this.pds.i18n.getText(this.pds.i18n.widgetKeys['dateWidget.open_picker_dialog']);
  }

  dateChanged(e): void {
    //Although we could directly set the modelValue, setting the value as the user would
    //and then calling inputChange goes through the same path that things go through when the user types in dates which
    //should be more maintanable.
    this.value = this.pds.i18n.localizeDate(e.target.value);
    this.inputChange();
    this.closeDatePickerDialog();
  };

  setValue(value, skip = false): void {
    if (value == null) {
      value = '';
    }
    let inputEl = this.querySelector('input');
    if (inputEl != null && value !== inputEl.value) {
      inputEl.value = value;
    }
    if (skip !== true) {
      this.setModelValue(this.pds.i18n.deLocalizeDate(value), true);
    }
  };

  /**
   * @param modelValue
   * @param skip
   * @returns returns if the model value was actually set.
   */
  setModelValue(modelValue: Date, skip = false): boolean {
    let ele = this.querySelector('input');
    if (ele != null && modelValue !== ele.modelValue) {
      ele.modelValue = modelValue;
      this.dialogValue = modelValue;
      if (skip !== true) {
        this.setValue(this.pds.i18n.localizeDate(modelValue), true);
      }
      return true;
    }
    return false;
  };

  openDatePickerDialog(): void {
    this.showDatePickerDialog = true;
  };

  closeDatePickerDialog(): void {
    this.showDatePickerDialog = false;
  };

  isValidDate(): boolean {
    return this.pds.i18n.isValidDate(this.value);
  };

  validateWidget(): boolean {
//                    datePreValidate(jQueryElement);

    if (this.value == null || this.value === '') {
      //TODO - make this thing handle the required rule.
      //short-circuit validation when value is empty
//                        if (jQueryElement.hasClass("required")) {
//                            //we only care about empty values when the field is required
//                            let ok = psRequired.validateRequired(jQueryElement);
//                            // if there were any errors, clear out the maxdate & mindate refs
//                            onErrorClearMinMaxDateReferences(jQueryElement);
//                            datePostValidate(jQueryElement);
//                            return ok;
//                        } else {
//                            //else the field is empty and it's not required... no other date things to check
//                            clearError(jQueryElement);
//                            //check if any special validations to run
//                            let specialValidationResults = psSpecial.validateSpecial(jQueryElement);
//                            // if there were any errors, clear out the maxdate & mindate refs
//                            onErrorClearMinMaxDateReferences(jQueryElement);
//                            datePostValidate(jQueryElement);
//                            return specialValidationResults;
//                        }

    }
    //if not empty - do the more interesting validations

    //check the date entry for validity
    let inputIsValid: boolean = this.isValidDate();
    let passedMin = true;
    let passedMax = true;

    if (inputIsValid) {
      //Gets the modelValue set in the input.
      this.setValue(this.value);
      if (this.dataMindate != null) {
        let minDateObj = new Date(this.dataMindate);
        let exclusiveMin = this.dataMindate_exclusive;
        passedMin = this.validateValueAfterMin(this.pds.utils.dateToTime(this.modelValue), this.pds.utils.dateToTime(minDateObj), exclusiveMin);
        if (!passedMin) {
          let errorMsg;
          if (exclusiveMin == null) {
            errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['dateWidget.failedMinDate'], this.pds.i18n.localizeDate(minDateObj));
          } else {
            errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['dateWidget.failedMinDateExclusive'], this.pds.i18n.localizeDate(minDateObj));
          }

          this.setError(errorMsg);
          inputIsValid = false;
        }
      }

      if (this.dataMaxdate != null) {
        let maxDateObj = new Date(this.dataMaxdate);
        let exclusiveMax = this.dataMaxdate_exclusive;
        passedMax = this.validateValueBeforeMax(this.pds.utils.dateToTime(this.modelValue), this.pds.utils.dateToTime(maxDateObj), exclusiveMax);
        if (!passedMax) {
          let errorMsg;
          if (exclusiveMax == null) {
            errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['dateWidget.failedMaxDate'], this.pds.i18n.localizeDate(maxDateObj));
          } else {
            errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['dateWidget.failedMaxDateExclusive'], this.pds.i18n.localizeDate(maxDateObj));
          }

          this.setError(errorMsg);
          inputIsValid = false;
        }
      }

      //TODO - Don't forget about zero dates.  We will want to do them as a custom function of this for PowerSchool.
    } else {
      let errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['dateWidget.invalidDate']);
      this.setError(errorMsg);
    }
    if (inputIsValid) {
      this.clearError();
      //TODO - special validation functions need to be registered with the powerSchoolDesignSystemToolkit library.
      //inputIsValid = psSpecial.validateSpecial(jQueryElement);
    }
    // if there were any errors, clear out the maxdate & mindate refs
    //onErrorClearMinMaxDateReferences(jQueryElement);
    //datePostValidate(jQueryElement);
    // this.isValid = inputIsValid;
    return inputIsValid;
  }
}



