import {PdsAbstractInputWidget} from './PdsAbstractInputWidget';
let isIntegerRegex = /^([-]?[0-9]*)?$/;

export class PdsNumberWidget extends PdsAbstractInputWidget {
  is = 'pds-number-widget';

  dataIsinteger: string;
  dataMinlength: string;
  dataMaxlength: string;
  dataMaxvalue: string;
  dataMinvalue: string;
  dataMaxdecimals: string;
  dataMinvalue_exclusive: string;
  dataMaxvalue_exclusive: string;

  properties = {
    dataIsinteger: String,
    dataMinlength: String,
    dataMaxlength: String,
    dataMaxvalue: String, //reads property data-maxvalue
    dataMinvalue: String,  //reads property data-minvalue
    dataMaxdecimals: String,
    dataMinvalue_exclusive: String,
    dataMaxvalue_exclusive: String,
    hasHadError: {
      value: false
    },
    name: String  //we construct the input id using this name.
  };

  // left commented out to help remember the life cycle hooks
  //
  // created() {
  //   //this might be useful too.
  // };
  //
  // attached() {
  //   console.log('pdsNumWidget Attached!');
  // };

  messageKeysUpdated(): void {

  }

  /**
   *
   * @param value - The string the user typed into the input.
   * @param skip - skips setting the model value. (Keeps from getting into recursive loops)
   */
  setValue(value: string, skip = false): void {
    if (value == null) {
      value = '';
    }

    let inputEl = this.querySelector('input');
    if (inputEl != null && value !== inputEl.value) {
      inputEl.value = value;
    }

    if (skip !== true) {
      this.setModelValue(this.pds.i18n.deLocalizeNumber(value), true);
    }
  };

  /**
   * @param modelValue
   * @param skip
   * @returns returns if the model value was actually set.
   */
  setModelValue(modelValue: number, skip: boolean): boolean {
    //TODO - Figure out how to tell typescript that I can access querySelector off of this.
    let ele = this.querySelector('input');
    if (ele != null && modelValue !== ele.modelValue) {
      ele.modelValue = modelValue;
      if (skip !== true) {
        this.setValue(this.pds.i18n.localizeNumber(modelValue), true);
      }
      return true;
    }
    return false;
  };

  isValidNumber(): boolean {
    return this.pds.i18n.isValidNumber(this.value);
  };

  validateIsInteger(): boolean {
    return isIntegerRegex.test(this.value);
  };

  validateWidget(): boolean {
    //numberPreValidate(jquerySelector);
    let inputIsValid = true;

    if (this.value == null || this.value === '') {
      //TODO - make this thing handle the required rule.
      //short-circuit validation when value is empty
//                    if (jquerySelector.hasClass('required')) {
//                        //we only care about empty values when the field is required
//                        return psRequired.validateRequired(jquerySelector); //handles clearing errors if necessary
//                    }
    } else { //if not empty - do the interesting number validations
      inputIsValid = this.isValidNumber();
      if (inputIsValid) {
        //Gets the modelValue set in the input.
        this.setValue(this.value);
        //check if isInteger and validate
        let isInteger = (this.dataIsinteger === 'true');
        let passedIntCheck = this.validateIsInteger();
        if (isInteger && !passedIntCheck) {
          let errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['numWidget.notAnInteger']);
          this.setError(errorMsg);
          inputIsValid = false;
        }

        //check minlength and validate
        let checkMinLength = this.dataMinlength != null;
        if (checkMinLength) {
          let passedMinLength = this.value.length >= parseInt(this.dataMinlength);
          if (!passedMinLength) {
            let errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['numWidget.failedMinlength'], this.dataMinlength);
            this.setError(errorMsg);
            inputIsValid = false;
          }
        }

        //check maxlength and validate
        let checkMaxLength = this.dataMaxlength != null;
        if (checkMaxLength) {
          let passedMaxLength = this.value.length <= parseInt(this.dataMaxlength);
          if (!passedMaxLength) {
            let errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['numWidget.failedMaxlength'], this.dataMaxlength);
            this.setError(errorMsg);
            inputIsValid = false;
          }
        }

        let value = this.modelValue; //a delocalized value. This should be a number, or null.
        //so far so good, check the range
        let minValue = this.dataMinvalue;
        let passedMin = true;
        let passedMax = true;
        if (minValue != null) {
          let exclusiveMin = this.dataMinvalue_exclusive;
          passedMin = this.validateValueAfterMin(value, parseFloat(minValue), exclusiveMin);
          if (!passedMin) {
            let errorMsg;
            if (exclusiveMin == null) {
              errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['numWidget.failedMinValue'], this.pds.i18n.localizeNumber(minValue));
            } else {
              errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['numWidget.failedMinValueExclusive'], this.pds.i18n.localizeNumber(minValue));
            }

            this.setError(errorMsg);
            inputIsValid = false;
          }
        }
        let maxValue = this.dataMaxvalue;
        if (passedMin) {
          if (maxValue != null) {
            let exclusiveMax = this.dataMaxvalue_exclusive;
            if (!this.validateValueBeforeMax(value, parseFloat(maxValue), exclusiveMax)) {
              let errorMsg;
              if (exclusiveMax == null) {
                errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['numWidget.failedMaxValue'], this.pds.i18n.localizeNumber(maxValue));
              } else {
                errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['numWidget.failedMinValueExclusive'], this.pds.i18n.localizeNumber(maxValue));
              }

              this.setError(errorMsg);
              inputIsValid = false;
            }
          }
        }

        //check maxdecimals and validate
        let checkMaxDecimals = this.dataMaxdecimals != null;
        if (inputIsValid && checkMaxDecimals) {
          let passedMaxDecimals = this.validateMaxDecimals(this.value);
          if (!passedMaxDecimals) {
            let errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['numWidget.failedMaxdecimals'], this.dataMaxdecimals);
            this.setError(errorMsg);
            inputIsValid = false;
          }
        }
      } else {
        let errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['numWidget.invalidNumber']);
        this.setError(errorMsg);
      }
    }
    //else its not required and empty - no checks needed
    if (inputIsValid) {
      this.clearError();
      //TODO - special validation functions need to be registered with the powerSchoolDesignSystemToolkit library.
      //inputIsValid = psSpecial.validateSpecial(jquerySelector);
    }
    return inputIsValid;
  };

  validateMaxDecimals(value): boolean {
    if (value == null) {
      value = this.modelValue;
    } else {
      value = this.pds.i18n.deLocalizeNumber(value);
    }
    let maxDecimals = parseInt(this.dataMaxdecimals);
    return (this.countDecimals(value) <= maxDecimals);
  }

  //determine the number of decimal places used in a delocalized number value
  countDecimals(value): number {
    if ((value % 1) === 0) {
      //integer
      return 0;
    }
    return value.toString().split('.')[1].length;
  };
}



