import {PdsWidget} from './PdsWidget';
import {CalendarDay} from './interfaces/calendar-day';
export class PdsDateWidgetDialog extends PdsWidget {

  is: string = 'pds-date-picker-dialog';

  monthLabels: Array<{value: string, text: string}>;
  pds: any;
  dayLabels: Array<string>;
  todayTimeStamp: number;
  dataMaxdate: string;
  maxDateTimeStamp: number;
  dataMindate: string;
  minDateTimeStamp: number;
  value: Date;

  //One object per day in the current month.
  days: Array<CalendarDay>;

  month: string;
  year: string;

  holdRefresh: boolean = false;
  possibleYears: Array<{value: string, text: string}>;

  //TODO search the code to see if this is actually being used.
  yearIncrement: boolean;

  properties = {
    month: {
      type: String,
      observer: 'monthChange'
    },
    year: {
      type: String,  //TODO update the rest of the page to use strings for year and month.
      observer: 'yearChange'
    },
    possibleYears: {
      type: Array
    },
    dataMaxdate: {
      type: String
    },
    dataMindate: {
      type: String
    }
  };

  setMonthLabels(): void {
    //when created, we need to generate the days to iterate over.
    let monthLabels: Array<{value: string, text: string}> = [];
    this.pds.i18n.locale.DATETIME_FORMATS.SHORTMONTH.forEach((month, index) => {
      let textValueObj = {
        value: String(index),
        text: month
      };
      monthLabels.push(textValueObj);
    });
    this.monthLabels = monthLabels;
  }

  ready(): void {
    super.ready();

    this.setMonthLabels();

    this.dayLabels = this.pds.i18n.locale.DATETIME_FORMATS.SHORTDAY;

    let today = new Date();
    today.setHours(0);
    today.setMinutes(0);
    today.setSeconds(0);
    today.setMilliseconds(0);
    this.todayTimeStamp = today.getTime();

    if (this.dataMaxdate != null) {
      this.maxDateTimeStamp = (new Date(this.dataMaxdate)).getTime();
    }
    if (this.dataMindate != null) {
      this.minDateTimeStamp = (new Date(this.dataMindate)).getTime();
    }

    let d;
    if (this.value != null) {
      d = this.value;
    } else {
      d = today;
    }

    this.holdRefresh = true;
    this.month = String(d.getMonth());
    this.holdRefresh = false;
    this.year = String(d.getFullYear());
  }

  // Functions left in to help remember which life cycle hooks are available.
  // created() {
  //   console.log('dialog created');
  // }
  //
  // attached() {
  //   console.log('dialog attached');
  // }
  //
  // detached() {
  //   console.log('dialog detached');
  // }

  //0-11 for index
  getSelectedMonth(index): boolean {
    return String(index) === this.month;
  }

  getSelectedYear(yearValue): boolean {
    return String(yearValue) === String(this.year);
  }

  getSelectedDateClass(dateTimeStamp, value): string | undefined {
    if (value != null && value.getTime() === dateTimeStamp) {
      return 'pds-selected-day';
    }
  }

  getTodayClass(dateTimeStamp): string | undefined {
    if (dateTimeStamp != null && dateTimeStamp === this.todayTimeStamp) {
      return 'pds-date-picker-today';
    }
  }

  getIsWeekendClass(isWeekend): string | undefined {
    if (isWeekend === true) {
      return 'pds-weekend-day';
    }
  }

  getInvisibleClass(dayNum): string | undefined {
    if (dayNum == null) {
      return 'pds-invisible';
    }
  }

  getDisabledItemClass(dateTimeStamp): string | undefined {
    if (this.isTimeStampDisabled(dateTimeStamp)) {
      return 'pds-disabled-day';
    }
  }

  isTimeStampDisabled(dateTimeStamp): boolean {
    if (dateTimeStamp != null) {
      if (this.maxDateTimeStamp != null && this.maxDateTimeStamp < dateTimeStamp) {
        return true;
      }

      if (this.minDateTimeStamp != null && this.minDateTimeStamp > dateTimeStamp) {
        return true;
      }
    }

    return false;
  }

  selectDate(event): void {
    let dateTimeAttr = event.target.getAttribute('data-date-time');
    let dateTimeStamp = new Date(parseInt(dateTimeAttr));

    if (this.isTimeStampDisabled(dateTimeStamp)) {
      return;
    }

    this.value = dateTimeStamp;

    //The value changed. Fire a valueChange event.
    let e = new Event('pds-date-change-event');
    (<any>this).dispatchEvent(e);
  }

  monthChange(): void {
    if (this.month != null && this.holdRefresh !== true) {
      this.updateDays();
    }
  }

  yearChange(): void {
    this.updateAvailableYears(this.year);
    this.updateDays();
  }

  updateAvailableYears(year): void {
    let newYears: Array<{value: string, text: string}> = [];
    for (let i = 0; i < 21; i++) {
      let val: string = String(year - 10 + i);
      newYears.push({value: val, text: val});
    }
    this.possibleYears = newYears;
  }

  previousMonth(): void {
    if (this.month === '0') {
      this.holdRefresh = true;
      this.month = '11';
      this.holdRefresh = false;
      this.year = String(parseInt(this.year) - 1);
    } else {
      this.month = String(parseInt(this.month) - 1);
    }
  }

  nextMonth(): void {
    if (this.month === '11') {
      this.holdRefresh = true;
      this.month = '0';
      this.holdRefresh = false;
      this.yearIncrement = true;
      this.year = String(parseInt(this.year) + 1);
      this.yearIncrement = false;
    } else {
      this.month = String(parseInt(this.month) + 1);
    }
  }

  setDate(dateTimeString): void {
    this.value = new Date(dateTimeString);
    this.updateDays();
  }

  updateDays(): void {
    this.days = this.pds.utils.generateDays(parseInt(this.year), parseInt(this.month));
  }

  messageKeysUpdated(): void {
    this.dayLabels = this.pds.i18n.locale.DATETIME_FORMATS.SHORTDAY;

    this.setMonthLabels();
  }
}



