//NOTE: This widget's definition is included in powerSchoolDesignSystemToolkit.ts
import {PdsAbstractInputWidget} from './PdsAbstractInputWidget';
import {pdsUtils} from './pdsUtils';

export class PdsTextWidget extends PdsAbstractInputWidget {
  is = 'pds-text-widget';

  //Text Labels

  dataRegexErrorText: string;

  //Options
  dataInputType: string;

  //Validation Rules
  dataRegex: string;
  dataMinlength: string;
  dataMaxlength: string;
  dataExcludechars: string;

  //Used to cache the regex so we don't keep re-creating it.
  validationRegex: RegExp;

  dataAdditionalInputAttributes: Object;
  dataAdditionalErrorAttributes: Object;

  properties = {
    name: String,
    dataLabelText: String,
    dataBadgeText: String,
    dataHelperText: String,
    dataRegexErrorText: String,
    dataFieldHelpText: String,
    dataPlaceholderText: String,
    dataTooltipText: String,
    dataInputType: {
      type: String,
      value: 'text'
    },
    dataRegex: {
      type: String,
      observer: 'setValidationRegex',
      notify: true
    },
    dataMinlength: String,
    dataMaxlength: String,
    dataExcludechars: String,
    dataIsrequired: {
      type: Boolean,
      value: false,
      observer: 'setRequired',
      notify: true
    },
    dataIsreadonly: {
      type: Boolean,
      value: false,
      observer: 'setReadonly',
      notify: true
    },
    hasHadError: {
      value: false,
      observer: 'setShowErrorMessage'
    },
    dataAdditionalInputAttributes: Object,
    dataAdditionalErrorAttributes: Object,
    dataHideUnelevatedBadge: {
      type: Boolean,
      value: false
    },
    dataDisableInvalidBadgeElevation: {
      type: Boolean,
      value: false
    }
  };

  messageKeysUpdated(): void {
  }

  /**
   * Initialize the widget's default values, visibility flags, and css styles.
   */
  attached(): void {
    super.attached();

    //Initialize the readonly style.
    this.setReadonly();

    // Initialize the required style.
    this.setRequired();

    // We need to store this for later incase we need to reset it.
    this.setOriginalBadgeText();

    // Initialize the visible state of the badge.
    this.setShowBadge(this.dataHideUnelevatedBadge, this.dataBadgeText);

    // Create and cache the regex object to be used later during validation.
    this.setValidationRegex(false);
  }

  ready(): void {
    // Some validation frameworks require special attributes to define validation types and messages.
    this.setAdditionalInputAttributes();

    this.setAdditionalErrorAttributes();
  }

  setAdditionalInputAttributes(): void {
    if (this.dataAdditionalInputAttributes != null) {
      let inputElement = this.querySelector('input');
      for (let key in this.dataAdditionalInputAttributes) {
        if (this.dataAdditionalInputAttributes.hasOwnProperty(key)) {
          inputElement.setAttribute(key, this.dataAdditionalInputAttributes[key]);
        }
      }
    }
  }

  setAdditionalErrorAttributes(): void {
    if (this.dataAdditionalErrorAttributes != null) {
      let errorElement = this.querySelector('.pds-validation');
      for (let key in this.dataAdditionalErrorAttributes) {
        if (this.dataAdditionalErrorAttributes.hasOwnProperty(key)) {
          errorElement.setAttribute(key, this.dataAdditionalErrorAttributes[key]);
        }
      }
    }
  }

  showLabel(dataLabelText: string): boolean {
    return (dataLabelText != null && dataLabelText.length > 0);
  }

  setOriginalBadgeText(): void {
    if (this.dataBadgeText != null) {
      this.originalBadgeText = this.dataBadgeText;
    }
  }

  /**
   *
   * @param value - The string the user typed into the input.
   * @param skip - skips setting the model value. (Keeps from getting into recursive loops)
   */
  setValue(value: string, skip = false): void {
    if (value == null) {
      value = '';
    }

    let inputEl = this.querySelector('input');
    if (inputEl != null && value !== inputEl.value) {
      inputEl.value = value;
    }

    if (skip !== true) {
      this.setModelValue(value, true);
    }
  }

  /**
   * @param modelValue
   * @param skip
   * @returns returns if the model value was actually set.
   */
  setModelValue(modelValue: string, skip: boolean): boolean {
    let ele = this.querySelector('input');
    if (ele != null && modelValue !== ele.modelValue) {
      ele.modelValue = modelValue;
      if (skip !== true) {
        this.setValue(modelValue, true);
      }
      return true;
    }
    return false;
  }

  validateWidget(): boolean {
    let inputIsValid = true;
    let textToValidate = this.value;
    let fieldBadge = this.querySelector('.pds-field-badge');

    inputIsValid = this.validateIsRequired(inputIsValid, this.dataIsrequired, textToValidate, fieldBadge, this.dataDisableInvalidBadgeElevation);

    if (this.value != null && this.value !== '') {
      inputIsValid = this.validateMinLength(inputIsValid, this.dataMinlength, textToValidate);
      inputIsValid = this.validateMaxLength(inputIsValid, this.dataMaxlength, textToValidate);
      inputIsValid = this.validateRegex(inputIsValid, this.validationRegex, textToValidate, this.dataRegexErrorText);
    }

    this.setIsValid(inputIsValid);

    if (inputIsValid) {
      this.clearError();
    }

    return inputIsValid;
  }

  validateIsRequired(isValid: boolean, isRequired: boolean, inputText: string, fieldBadge: HTMLElement, disableInvalidBadgeElevation: boolean): boolean {
    if (isValid && isRequired) {
      if (inputText == null || inputText === '') {
        this.toggleFieldBadge(fieldBadge, true, !disableInvalidBadgeElevation);
        isValid = false;
        this.setIsValid(isValid);
      } else {
        this.toggleFieldBadge(fieldBadge, !this.dataHideUnelevatedBadge, false);
      }
    }
    return isValid;
  }

  validateMinLength(isValid: boolean, minLength: string, inputText: string): boolean {
    if (isValid) {
      let checkMinLength = minLength != null;
      if (checkMinLength) {
        let passedMinLength = inputText.length >= parseInt(minLength);
        if (!passedMinLength) {
          let errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['textWidget.failedMinlength'], minLength);
          this.setError(errorMsg);
          isValid = false;
        }
      }
    }
    return isValid;
  }

  validateMaxLength(isValid: boolean, maxLength: string, inputText: string): boolean {
    if (isValid) {
      let checkMaxLength = maxLength != null;
      if (checkMaxLength) {
        let passedMaxLength = inputText.length <= parseInt(maxLength);
        if (!passedMaxLength) {
          let errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['textWidget.failedMaxlength'], maxLength);
          this.setError(errorMsg);
          isValid = false;
        }
      }
    }
    return isValid;
  }

  validateRegex(isValid: boolean, regex: RegExp, inputText: string, regexErrorMessage: string): boolean {
    if (isValid) {
      let checkRegex = regex != null;
      if (checkRegex) {
        if (!regex.test(inputText)) {
          let errorMsg = '';
          if (regexErrorMessage != null) {
            errorMsg = regexErrorMessage;
          } else {
            errorMsg = this.pds.i18n.getText(this.pds.i18n.widgetKeys['textWidget.failedRegex']);
          }
          this.setError(errorMsg);
          isValid = false;
        }
      }
    }
    return isValid;
  }

  /**
   * Creates a RegExp object and caches it for reuse. This function is called
   * by the this.dataRegex observer to reset the cached value whenever the
   * dataRegex attribute is changed.
   *
   * @param validate - Used to skip validation after changing the regex.
   */
  setValidationRegex(validate = true): void {
    if (this.dataRegex != null && this.dataRegex.length > 0) {
      this.validationRegex = new RegExp(this.dataRegex);
    } else {
      this.validationRegex = new RegExp('');
    }
    if (validate) {
      this.validateWidget();
    }
  }

  /**
   * Handles the keypress event for the input.
   * Primarly used to honor dataExcludedChars property.
   *
   * @param event
   * @param detail
   * @param sender
   */
   //TODO ask Sam if this is Ok behavior.
  handleKeypress(event, detail, sender): boolean {
    if (this.dataExcludechars != null && this.dataExcludechars.length > 0) {
      let enteredCharacter = String.fromCharCode(event.keyCode);
      if (this.isExcludedCharacter(enteredCharacter, this.dataExcludechars)) {
        event.preventDefault();
        event.stopPropagation();
        return false;
      }
    }
    return true;
  }

  /**
   * Used to determine if a given character is in the excludedCharacters collection.
   *
   * @param enteredCharacter - The character to test with.
   * @param excludedCharacters - The string of characters to check against.
   */
  isExcludedCharacter(enteredCharacter: string, excludedCharacters: string): boolean {
    for (let i = 0; i < excludedCharacters.length; i++) {
      if (enteredCharacter === excludedCharacters[i]) {
        return true;
      }
    }
    return false;
  }

  toggleTooltip(): void {
    //TODO - The tooltip functionality is to be done in it's own component.
    return;
  }
}



