export let pdsLogger = {
  setLoggerStatus: function (loggerName: string, showMessages: boolean): void {
    this.loggerStatus[loggerName] = showMessages;
  },
  //Key: the logger name. Value: true if logs will show up, false if they will not show up.
  loggerStatus: {},
  requiresLogger: function (functionName: string): void {
    //only register things that are here.
    if (this.log[functionName] == null) {
      let loggerFunction = function (msg: any, ...params: Array<any>): void {
        if (this.loggerStatus[functionName]) {
          console.log.apply(console, arguments);
        }
      };
      this.log[functionName] = loggerFunction.bind(this);
      //We only want to turn it off if someone has not said they want it.
      if (!this.loggerStatus.hasOwnProperty(functionName)) {
        this.loggerStatus[functionName] = false;
      }
    }
  },
  log: function (msg: any, ...params: Array<any>): void {
    if ((<any>this.loggerStatus).log) {
      console.log.apply(console, arguments);
    }
  }
};



