//This is kept private to keep the library footprint small.
let formatNumber = function(format, number: number): string | undefined {
  let ds = format.ds; // decimal separator
  let result = '';

  //curiously, isNaN(null) returns false.
  if (isNaN(number) || number == null) {
    if (number == null) {
      return;
    }

    return String(number);
  } else {
    result = number.toString();
    result = result.replace('.', ds);
    return result;
  }
};

//Returns the string representation of number left-padded with enough zeros to make it the length of padding
//Returns just the string representation of number if it is already longer than padding
let padZeros = function(number: number, padding: number): string | undefined {
  let paddedString = '';
  if (isNaN(number) || isNaN(padding) || number == null || padding == null) {
    if (number == null) {
      return;
    }

    return String(number);
  } else {
    paddedString = number.toString();
    while (paddedString.length < padding) {
      paddedString = '0' + paddedString;
    }

    return paddedString;
  }
};

export let pdsI18n = {
  setLocaleAndWidgetKeys: function(locale, widgetKeys?): void {
    this.locale = locale;
    if (widgetKeys != null) {
      /* tslint:disable */
      for (let key in widgetKeys) {
        this.widgetKeys[key] = widgetKeys[key];
      }
      /* tslint:enable */
    }
    this.isUpdatedMessageKeys = true;
    let elements = document.querySelectorAll('.default-messages');
    (<any>elements).forEach((element: any) => {
      if (typeof element.messageKeysUpdated === 'function') {
        element.messageKeysUpdated();
        element.removeMessgeKeyUpdatedClass();
      }
    });
  },
  isUpdatedMessageKeys: false,
  twoDigitRegex: /^\d{2}$/,
  pds: undefined,
  //This object may be populated with message keys for translation on pages that aren't using Angular 2 to do
  //translation.
  messageKeys: {},
  //These messages can be overridden by message keys that we would want to use instead of the defaults.
  //Note: running this.getText('numWidget.tooLarge') will not return 'Too Large'.
  widgetKeys: {
    'numWidget.tooLarge': 'Too Large',
    'numWidget.notAnInteger': 'Not an Integer',
    'numWidget.invalidNumber': 'Not a Valid Number',
    'numWidget.failedMinlength': 'Number must be at least {{arg0}} digits in length',
    'numWidget.failedMaxdecimals': 'Number cannot have more than {{arg0}} decimal places',
    'numWidget.failedMinValue': 'Number cannot be less than {{arg0}}',
    'numWidget.failedMaxValue': 'Number cannot be greater than {{arg0}}',
    'numWidget.failedMinValueExclusive': 'Number cannot be less than or equal to {{arg0}}',
    'numWidget.failedMaxValueExclusive': 'Number cannot be greater than or equal to {{arg0}}',
    'dateWidget.invalidDate': 'Not a Valid Date',
    'dateWidget.failedMinDate': 'Date cannot be before {{arg0}}',
    'dateWidget.failedMaxDate': 'Date cannot be after {{arg0}}',
    'dateWidget.failedMinDateExclusive': 'Date cannot be on or before {{arg0}}',
    'dateWidget.failedMaxDateExclusive': 'Date cannot be on or after {{arg0}}',
    'dateWidget.open_picker_dialog': 'Open Date Picker',
    'textWidget.failedMinlength': 'Text must be at least {{arg0}} characters in length',
    'textWidget.failedMaxlength': 'Text must not be greater than {{arg0}} characters in length',
    'textWidget.failedRegex': 'Text does not match required pattern',
    'textWidget.required': 'required'
  },
  //This stores the locale information required by the widgets.  Updated it depending on what locale you are in.
  locale: {
    'LOCALE': 'en_US',
    'DATETIME_FORMATS': {
      'MONTH': ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
      'SHORTMONTH': ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
      'medium': 'MM/dd/yyyy hh:mm:ss a',
      'mediumDateText': 'MM/DD/YYYY',
      'shortDateText': 'MM/DD/YY',
      'AMPMS': ['AM', 'PM'],
      'mediumDate': 'MM/dd/yyyy',
      'mediumTime': 'hh:mm:ss a',
      'shortTime': 'hh:mm a',
      'short': 'M/d/yy hh:mm a',
      'SHORTDAY': ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
      'DAY': ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
      'shortDate': 'M/d/yy'
    },
    '$BREACH_MITIGATION': 'XRFmEENA55t627cN',
    'NUMBER_FORMATS': {
      'DECIMAL_SEP': '.',
      'number_regex': '([-]?(([0-9]{1,3}(,[0-9]{3})*(\\.[0-9]+)?|\\.[0-9]+)|([0-9]*(\\.[0-9]+)?)))',
      'CURRENCY_SYM': '$',
      'GROUP_SEP': ',',
      'PATTERNS': [{
        'negSuf': '',
        'posSuf': '',
        'lgSize': '3',
        'minFrac': 0,
        'minInt': 1,
        'negPre': '-',
        'gSize': 3,
        'maxFrac': 9,
        'macFrac': '0',
        'posPre': ''
      }],
      'number_format': '#,###.#########'
    }
  },
  overWriteGetText(getTextOverwriteFunction): void {
    this.getTextOverwriteFunction = getTextOverwriteFunction;
  },
  getShortFormat: function(): void {
    return this.locale.DATETIME_FORMATS.shortDate;
  }, getDateFormat: function(): void {
    return this.locale.DATETIME_FORMATS.mediumDate;
  }, getDateText: function(): string {
    return this.locale.DATETIME_FORMATS.mediumDateText;
  },
  /**
   * This function is expceded to be overridden by applications using Angular.
   * @param key
   * @param params
   * @returns {any}
   */
    getText(key: string, params: string | Array<string>): string | undefined {

    //This is for Angular2 to use ng-translate instead of the default function.
    if (this.getTextOverwriteFunction != null) {
      return this.getTextOverwriteFunction(key, params);
    }

    if (typeof key !== 'string') {
      return;
    }

    let msg = this.messageKeys[key];
    if (msg == null) {
      msg = key;
    }

    if (params != null) {
      if (!(params instanceof Array)) {
        //Ha ha! Now it is an array of strings.
        params = [<string>params];
      }

      if (params.length > 0) {
        for (let i = 0; i < params.length; i++) {
          let token = '{{arg' + i + '}}';
          //Set param[i] as a string just in case it slips through.
          msg = msg.replace(token, String(params[i]));
        }
      }
    }

    return msg;
  },
  localizeDate: function(d: Date, fmt?): string {
    if (d == null) {
      return '';
    }

    if (!this.isValidDateObject(d)) {
      return String(d);
    }

    let seperator = this.locale.DATETIME_FORMATS.mediumDate.match(/[^A-Za-z]/);
    let patternPieces = this.locale.DATETIME_FORMATS.mediumDate.split(seperator[0]);
    let localizedStrings: Array<number> = [];

    for (let i = 0; i < 3; i++) {
      switch (patternPieces[i]) {
        case 'd':
          localizedStrings.push(d.getDate());
          break;
        case 'dd':
          localizedStrings.push(d.getDate());
          break;
        case 'M':
          localizedStrings.push(d.getMonth() + 1);
          break;
        case 'MM':
          localizedStrings.push(d.getMonth() + 1);
          break;
        case 'yy':
          if (fmt !== 'mmdd') {
            localizedStrings.push(d.getFullYear());
          }
          break;
        case 'yyyy':
          if (fmt !== 'mmdd') {
            localizedStrings.push(d.getFullYear());
          }
          break;
        default:
          console.error('Unexpected format while localizing date');
      }
    }
    return localizedStrings.join(seperator);
  },
  deLocalizeDate: function(localizedDateString: string): Date | undefined {
    if (localizedDateString == null || localizedDateString === '') {
      return;
    }

    let seperator = this.locale.DATETIME_FORMATS.mediumDate.match(/[^A-Za-z]/);
    let patternPieces = this.locale.DATETIME_FORMATS.mediumDate.split(seperator[0]);
    let localizedPieces = localizedDateString.split(seperator);

    if (localizedPieces.length === 3) {
      let day;
      let month;
      let year;
      for (let i = 0; i < 3; i++) {
        switch (patternPieces[i]) {
          case 'd':
          case 'dd':
            day = localizedPieces[i];
            break;
          case 'M':
          case 'MM':
            month = localizedPieces[i];
            break;
          case 'yy':
          case 'yyyy':
            year = localizedPieces[i];
            if (this.twoDigitRegex.test(year)) {
              if (parseInt(year) > 50) {
                year = '19' + year;
              } else {
                year = '20' + year;
              }
            }
            break;
          default:
            console.error('Unexpected format when delocalizing date.');
        }
      }
      //  /.-,
      let d = new Date(year, month - 1, day, 0, 0, 0);

      if (this.isValidDateObject(d)) {
        return d;
      }
    }
  },
  /**
   * @param d - a javascript date object
   */
  isValidDateObject: function(d: Date): boolean {
    if (d instanceof Date) {
      return !isNaN(d.getTime()); //invalid date object will return NaN for getTime
    }
    return false;
  },
  /**
   * Is the localized date string a valid date?
   * TODO - Should passing in '' evaluate to a valid date?  Maybe not so much.
   * @param localizedDate
   * @returns {any}
   */
  isValidDate: function(localizedDate: string): boolean {
    if (localizedDate !== '') {
      let parsedDate: Date = this.deLocalizeDate(localizedDate);
      return this.isValidDateObject(parsedDate);
    }
    return true;
  },
  /**
   * @param psJsonDateString - String - in the form of yyyy-mm-dd or yyyy-mm-dd hh:mm:ss
   * @param preserceTimeOfDay - boolean - optional param to retain hh:mm:ss in returned date
   * object. Default is false if not passed.
   * @returns {*}
   */
  parseJsonDate: function(psJsonDateString: string, preserveTimeOfDay?: boolean): Date | undefined {
    if (psJsonDateString == null) {
      return;
    }

    let year = 0;
    let month = 0;
    let date = 0;
    let hours = 0;
    let minutes = 0;
    let seconds = 0;

    let split = psJsonDateString.split('-');
    if (split.length < 3) {
      return;
    }
    //a valid jsonDateString may contain a time of date hour/minute/seccond
    let dayTimeOfDay = split[2].split(' ');
    if (dayTimeOfDay.length === 2) {
      //time of day is present
      split[2] = dayTimeOfDay[0];
      if (preserveTimeOfDay) {
        let timeOfDay = dayTimeOfDay[1].split(':');
        if (timeOfDay.length === 3) {
          hours = parseInt(timeOfDay[0]);
          minutes = parseInt(timeOfDay[1]);
          seconds = parseInt(timeOfDay[2]);
        }
        //else invalid hh:mm:ss - ignore it
      }
    }
    year = parseInt(split[0]);
    month = parseInt(split[1]) - 1;
    date = parseInt(split[2]);
    let returnDate = new Date(year, month, date, hours, minutes, seconds);
    if (isNaN(returnDate.getTime())) {
      return;
    }
    return returnDate;
  },
  /**
   * Sister function of parseJsonDate.  Converts a date object into the JsonDateString format
   * necessary for XTE.
   */
  getJsonDate: function(javascriptDateObject: Date, preserveTimeOfDay?: boolean): string | undefined {
    if (javascriptDateObject == null) {
      return;
    }

    if (isNaN(javascriptDateObject.getTime())) {
      return;
    }

    let psJsonDateString = javascriptDateObject.getFullYear() + '-' +
      padZeros((javascriptDateObject.getMonth() + 1), 2) + '-' +
      padZeros(javascriptDateObject.getDate(), 2);

    if (preserveTimeOfDay) {
      psJsonDateString += ' ' + padZeros(javascriptDateObject.getHours(), 2) + ':' +
        padZeros(javascriptDateObject.getMinutes(), 2) + ':' +
        padZeros(javascriptDateObject.getSeconds(), 2);
    }

    return psJsonDateString;
  },
  /**
   * Converts a JSON date to a non localized date
   * @param psJsonDateString - String - in the form of yyyy-mm-dd
   * @returns - String in the form of MM/DD/YYYY
   */
  parseJsonDateToNonLocalized: function(psJsonDateString: string): string {
    let date = this.parseJsonDate(psJsonDateString);
    return this.getMmDdYyyyDateString(date);
  },
  /**
   * Gets the date in the form MM/DD/YYYY
   * @param date The date object to convert.
   */
    getMmDdYyyyDateString(date: Date): string {
    return (date.getMonth() + 1) + '/' + date.getDate() + '/' + date.getFullYear();
  },
  addDaysToDate: function(date, numDays): Date {
    date.setDate(date.getDate() + numDays);
    return date;
  },
  getLocalizedDayOfWeek: function(date): string {
    return this.locale.DATETIME_FORMATS.DAY[date.getDay()];
  },
  getNumericFormat: function(): {
    format: string,
    size: string,
    gs: string,
    ds: string,
    regex: string
  } {
    //{ format: '#,##0.#########', size: '3', gs: ',', ds: '.', regex: '([-]?(([0-9]{1,3}(,[0-9]{3})*(\.[0-9]+)?|\.[0-9]+)|([0-9]*(\.[0-9]+)?)))' }
    return {
      format: this.locale.NUMBER_FORMATS.number_format,
      size: this.locale.NUMBER_FORMATS.PATTERNS[0].gSize,
      gs: this.locale.NUMBER_FORMATS.GROUP_SEP,
      ds: this.locale.NUMBER_FORMATS.DECIMAL_SEP,
      regex: this.locale.NUMBER_FORMATS.number_regex
    };
  },
  /**
   * Localize a number, and optionally round it to a specific number of decimal places
   * @param number, number to localize
   * @param decimalPlaces (optional), number of decimal places to round to
   * @returns the localized version of the number
   */
  localizeNumber: function(number: number, decimalPlaces): string | undefined {
    if (number == null) {
      return;
    }

    if (decimalPlaces != null && !isNaN(decimalPlaces)) {
      // round the number to n decimal places before localizing
      let multiplier = 1;
      for (let i = 0; i < decimalPlaces; i++) {
        multiplier *= 10;
      }
      number = Math.round(number * multiplier) / multiplier;
    }
    return formatNumber(this.getNumericFormat(), number);
  },
  deLocalizeNumber: function(number: string): number {
    return this.removeNumberFormat(this.getNumericFormat(), number);
  },
  deLocalizeNumberToString: function(number: string): string | undefined {
    let result = this.removeNumberFormat(this.getNumericFormat(), number);
    if (result == null) {
      return;
    }
    return String(result);
  },
  removeNumberFormat: function(format: any, number: any): number | undefined {
    if (number == null) {
      return;
    }

    // IE does not like instantiating RegExp objects with empty values, so we have to
    // check for valid values in the grouping separator before using it in a RegExp.
    let gsNotBlank = (format.gs === '.' || format.gs === ',' || format.gs === ' ');
    number = number.toString();
    // We also want to check that only valid characters are present.
    let regexString = '^' + format.regex + '$';
    let validNumber = new RegExp(regexString, 'g');
    if (number !== '' && number.match(validNumber) != null) {
      if (gsNotBlank) {
        let gs = new RegExp('\\' + format.gs, 'g');
        number = number.replace(gs, '');
      }
      let ds = new RegExp('\\' + format.ds, 'g'); //The decimal separator is not expected to be empty
      number = parseFloat(number.replace(ds, '.'));
    } else {
      return;
    }

    if (!isNaN(number)) {
      return number;
    }
  },
  isValidNumber: function(numValue: string): boolean {
    if (numValue !== '') {
      let regexString = '^' + this.locale.NUMBER_FORMATS.number_regex + '$';
      let regex = new RegExp(regexString, 'g');
      if (regex.test(numValue)) {
        return (this.deLocalizeNumber(numValue) != null);
      } else {
        return false;
      }
    }
    return true;
  },
  /**
   * Adds a new field for an object with the value localized.
   * This function will add a new field to the obejct with the localized value.
   * @param object An object with a number value that needs to be localized
   * @param key The key of the value that needs to be localized.
   */
  localizeNumericObject: function(object: Object, key: string): void {
    object['_i18n_' + key] = this.localizeNumber(object[key]);
  },
  /**
   * This is the sister function of localizeNumericObject. If the localized value
   * can be parsed, it will update the original value with the deLocalized version.
   * @param object An object with a number value that has been localized using localizeNumericObject
   * @param key The key of the value that got localized.
   */
  deLocalizeNumericObject: function(object: Object, key: string): void {
    if (!object.hasOwnProperty('_i18n_' + key)) {
      return;
    }

    if (object['_i18n_' + key] == null || object['_i18n_' + key] === '') {
      object[key] = '';
      return;
    }

    object[key] = this.deLocalizeNumber(object['_i18n_' + key]);
  },
  /**
   * Generate a localized time string from a javascript date object.
   * @param time - javascript date object
   * @returns {string}
   */
  makeStrFromTime: function(time: Date): string {
    let separator = ':';
    let timeFormat = this.locale.DATETIME_FORMATS.shortTime;
    let ampmNames = this.locale.DATETIME_FORMATS.AMPMS;
    let show24Hours = (timeFormat === 'HH:mm') ? true : false;
    let hours: number = time.getHours();
    let mins: number = time.getMinutes();
    let hoursString: string;
    let minsString: string;

    if (!show24Hours) {
      hours = ((hours + 11) % 12) + 1;
    }
    if (hours < 10) {
      hoursString = '0' + hours;
    } else {
      hoursString = String(hours);
    }

    if (mins < 10) {
      minsString = '0' + mins;
    } else {
      minsString = String(mins);
    }
    return hours + separator + mins + (show24Hours ? '' : ' ' + ampmNames[time.getHours() < 12 ? 0 : 1]);
  }
};



