import {PdsWidget} from './PdsWidget';
import {pdsUtils} from './pdsUtils';

export abstract class PdsAbstractInputWidget extends PdsWidget {

  name: string;

  errorMessage: string = '';
  hasHadError: boolean = false;
  isValid: boolean = true;

  //Validation rules
  dataIsreadonly: boolean;
  dataIsrequired: boolean;

  //Validation Options
  dataHideUnelevatedBadge: boolean;
  dataDisableInvalidBadgeElevation: boolean;

  //Visibility Flags
  showBadge: boolean;
  showTooltip: boolean = false;
  showError: boolean = false;

  //Text labels
  dataLabelText: string;
  dataBadgeText: string;
  dataHelperText: string;
  dataFieldHelpText: string;
  dataPlaceholderText: string;
  dataTooltipText: string;

  originalBadgeText: string = '';

  inputChange(): void {
    this.validateWidget();
    this.setValue(this.value);
    (<any>this).dispatchEvent(new Event('modelValueChange'));
  }

  validateValueAfterMin(value, minValue, exclusive): boolean {
    if (value != null && minValue != null) {
      if (value < minValue) {
        return false;
      }
      if (exclusive != null) {
        if (value <= minValue) {
          return false;
        }
      }
    }
    return true;
  }

  validateValueBeforeMax(value, maxValue, exclusive): boolean {
    if (value != null && maxValue != null) {

      if (value > maxValue) {
        return false;
      }
      if (exclusive != null) {
        if (value >= maxValue) {
          return false;
        }
      }
    }
    return true;
  }

  setError(errorMsg): void {
    if (errorMsg != null && errorMsg !== '') {
      this.errorMessage = errorMsg;
      this.setIsValid(false);
    } else {
      this.errorMessage = '';
      this.setIsValid(true);
    }
  }

  setIsValid(isValid): void {
    this.isValid = isValid;
  }

  clearError(): void {
    this.errorMessage = '';
    this.setIsValid(true);
  }

  errorClass(hasHadError, errorMessage): string | undefined {
    if (hasHadError && ((errorMessage != null && errorMessage !== '') || !this.isValid)) {
      return 'error';
    }
  }

  hasError(): boolean {
    return !this.isValid || (this.errorMessage != null && this.errorMessage !== '');
  }

  focusLost(): void {
    if (this.hasError()) {
      this.hasHadError = true;
    }
  }

  get value(): any {
    let result = this.querySelector('input');
    if (result != null) {
      return result.value;
    }
  }

  set value(value) {
    this.setValue(value);
  }

  get modelValue(): any {
    let result = this.querySelector('input');

    if (result != null) {
      return result.modelValue;
    }
  }

  set modelValue(value) {
    let modelValueChanged = this.setModelValue(value);
    if (modelValueChanged) {
      this.validateWidget();
    }
  }

  abstract setValue(value: any, skip?: boolean): void;

  abstract setModelValue(value: any, skip?: boolean): void;

  abstract validateWidget(): boolean;

  attached(): void {
    let valueAttr = (<any>this).getAttribute('value');
    if (valueAttr != null) {
      this.value = valueAttr;
      (<any>this).removeAttribute('value');
    }
  }

  /**
   * Helper function to dynamically add visibility classes to the badge element.
   */
  getBadgeClasses(dataHideUnelevatedBadge: boolean, dataBadgeText: string): string {
    this.setShowBadge(dataHideUnelevatedBadge, dataBadgeText);
    let showTheBadge = this.showBadge;
    let classes = 'pds-field-badge';
    if (!showTheBadge) {
      classes += ' pds-is-hidden';
    }
    return classes;
  }

  setShowBadge(dataHideUnelevatedBadge: boolean, dataBadgeText: string): void {
    this.showBadge = (!dataHideUnelevatedBadge && dataBadgeText != null && dataBadgeText.length > 0);
  }

  /**
   * Sets the flag used to determine if the error message should be displayed.
   * This checks both hasHadError and if errorMessage is populated because
   * when required has an invalid state, we are showing the badge near the label instead
   * of an error below the input. We want to keep the error message element hidden in that case.
   */
  setShowErrorMessage(): void {
    this.showError = this.hasHadError && this.errorMessage !== '';
  }

  setRequired(): void {
    if (this.dataIsrequired != null) {
      let outerDiv = this.querySelector('div.pds-input-text');
      if (this.dataIsrequired) {
        this.querySelector('input').setAttribute('required', 'required');
        pdsUtils.addClass(outerDiv, 'pds-is-required');
        // Check if the actual badge text attribute exists.
        // Because of the way the control initializes, this.dataBadgeText
        // could be null while there is actually a value defined for the attribute.
        if (!outerDiv.parentElement.hasAttribute('data-badge-text')) {
          this.dataBadgeText = this.pds.i18n.getText(this.pds.i18n.widgetKeys['textWidget.required']);
        }
      } else {
        this.querySelector('input').removeAttribute('required', 'required');
        pdsUtils.removeClass(outerDiv, 'pds-is-required');
        if (this.originalBadgeText != null && this.originalBadgeText !== '') {
          this.dataBadgeText = this.originalBadgeText;
        }

        if (this.getBadgeClasses != null) {
          pdsUtils.addClass(this.querySelector('.pds-field-badge'), this.getBadgeClasses(this.dataHideUnelevatedBadge, this.dataBadgeText));
        }
      }
    }
  }

  setReadonly(): void {
    if (this.dataIsreadonly != null && this.value != null) {
      let outerDiv = this.querySelector('div.pds-input-text');
      if (this.dataIsreadonly || this.dataIsreadonly.toString().toLowerCase() !== 'false') {
        pdsUtils.addClass(outerDiv, 'pds-is-readonly');
        this.querySelector('.pds-readonly-data').innerText = this.value;
      } else {
        pdsUtils.removeClass(outerDiv, 'pds-is-readonly');
        this.querySelector('.pds-readonly-data').innerText = '';
      }
    }
  }

  showLabel(dataLabelText: string): boolean {
    return (dataLabelText != null && dataLabelText.length > 0);
  }

  showFieldHelp(dataFieldHelpText: string): boolean {
    return (this.dataTooltipText != null && this.dataTooltipText.length > 0);
  }

  showHelperText(dataHelperText: string): boolean {
    return (this.dataHelperText != null && this.dataHelperText.length > 0);
  }

  toggleFieldBadge(fieldBadge: HTMLElement, showBadge: boolean, isElevated?: boolean): void {
    if (showBadge) {
      pdsUtils.removeClass(fieldBadge, 'pds-is-hidden');
    } else {
      pdsUtils.addClass(fieldBadge, 'pds-is-hidden');
    }

    if (isElevated != null) {
      this.elevateFieldBadge(fieldBadge, isElevated);
    }
  }

  elevateFieldBadge(fieldBadge: HTMLElement, isElevated: boolean): void {
    if (isElevated) {
      pdsUtils.addClass(fieldBadge, 'pds-is-elevated');
    } else {
      pdsUtils.removeClass(fieldBadge, 'pds-is-elevated');
    }
  }

}



